//@import 'bourbon';
@import "bootstrap/bootstrap.less";
@import "custom_fonts.less";
@import "custom_reset.less";
@import "custom_variables.less";
@import "custom_mixins.less";
@import "weather_logos.less";

/* General styles section */
body {
	background: none @bodyBg;
	font-size: @bodyFontSize;
	line-height: @bodyLineHeight;
	font-family: @bodyFontFamily;
	color: @bodyFontColor;
}

h1, h2, h3, h4, h5, h6 {
	font-weight: 600;
}

h2 {
	font-size: 30px;
	text-transform: uppercase;
	margin-bottom: 15px;
	@media (max-width: @screen-xs-max) {
		font-size: 20px;
	}
}

h3.title {
	color: @titleColor;
	text-transform: uppercase;
	font-weight: 700;
	font-size: 22px;
	margin-bottom: 10px;
	a {
		color: @titleColor;
	}
}

a {
	.transition(150ms);
}

.container {
	max-width: 970px;
}


/* MISCELLANEOUS STUFF */
.mobile_separator {
	margin-bottom: 25px;
}

.col-custom-7 {
	width: 14.2857%;
	float: left;
	padding-left: 15px;
	padding-right: 15px;
}

.forecast_title {
	color: @titleColor;
	text-transform: uppercase;
	margin-bottom: 20px;
}

.buttons_holder {
	.clearfix();
	margin-bottom: 30px;
	.custom_btn {
		@media (min-width: @screen-sm-min) {
			float: left;
		}
	}
}

.white_box {
	background: none @whiteBoxBg;
    border: 1px solid @holdersBorderColor;
    border-radius: 6px;
    margin-bottom: 30px;
    padding: 15px 0;
    .clearfix();
    .title_holder {
    	width: 100%;
    	.clearfix();
    }
    .title {
    	padding-left: 15px;
    }
}


/* BUTTONS styles */
.custom_btn {
	border: none;
	box-shadow: none;
	border-radius: @customBtnBorderRadius;
	color: @customBtnColor;
	font-size: @customBtnFontSize;
	font-weight: @customBtnFontWeight;
	text-align: center;
	line-height: @customBtnLineHeight;
	padding: @customBtnPadding;
	background-size: 1px 60px;
	.transition(150ms);
	&:hover {
		color: @customBtnColor;
		background-position: 30px;
	}
	&.custom_btn_green {
		#gradient > .vertical(@start-color: @customBtnGreenGradientFrom; @end-color: @customBtnGreenGradientTo;);
	}
	&.custom_btn_blue {
		#gradient > .vertical(@start-color: @customBtnBlueGradientFrom; @end-color: @customBtnBlueGradientTo;);
	}
	&.custom_btn_orange {
		#gradient > .vertical(@start-color: @customBtnOrangeGradientFrom; @end-color: @customBtnOrangeGradientTo;);
	}
	& + .custom_btn {
		margin-left: 8px;
		@media (max-width: @screen-xs-max) {
			margin-left: 3px;
		}
	}
	&.mobile_small {
		padding: @customBtnPaddingMobile;
		font-size: @customBtnFontSizeMobile;
		margin-left: 3px;
		margin-right: 3px;
		&:first-child {
			margin-left: 0;
		}
		&:last-child {
			margin-right: 0;
		}
	}
}

/* SWITCHERS styles */
.switchers_holder {
    > li {
      	float: left;
    }
    .switcher {
    	padding: 4px;
	    border-radius: 6px;
	    margin-right: 10px;
	    float: left;
	    border: none;
	    line-height: 16px;
	    #gradient > .vertical(@start-color: @switchersItemBgGradientFrom; @end-color: @switchersItemBgGradientTo;);
	    @media (max-width: @screen-xs-max) {
	        margin-right: 7px;
	    }
	    .btn {
	        font-size: 12px;
	        line-height: 12px;
	        padding: 2px;
	        border-radius: 4px !important;
	        border: none;
	        background: none;
	        color: @switcherColor;
	        &.active {
	          	cursor: default;
	          	color: @switcherActiveColor;
	          	#gradient > .vertical(@start-color: @switcherActiveBgGradientFrom; @end-color: @switcherActiveBgGradientTo;);
	        }
	        &:first-child {
	          	margin-right: 5px;
	        }
	    }
    }
    .lang_switcher {
    	background: none @languageSwitcherBg;
        margin-right: 0;
        border: none;
        border-radius: 6px;
        .caret {
          	color: @languageSwitcherCaretColor;
          	float: left;
          	margin: 6px 5px 0 0;
        }
    }
    .dropdown-menu {
      	border-radius: 6px;
      	min-width: 10px;
      	position: absolute;
      	background: none @languageSwitcherBg;
      	left: auto;
      	right: 0;
      	> li {
        	> a {
          		padding: 3px 10px;
        	}
      	}
    }
}


/* COMPASS */
.compass_directions {
	background-image: url('../images/elements/compass_directions.svg');
	background-repeat: no-repeat;
	background-size: 100%;
	width: 3.375em;
	height: 3.375em;
	display: inline-block;
	&.direction_w {
		background-position: center 0;
	}
	&.direction_wws {
		background-position: center -3.375em;
	}
}


/* HEADER styles */
header {
	padding: 5px 0 0 0;
	background: none @headerBg;
	.logo_holder {
		a {
			display: block;
			@media (max-width: @screen-xs-max) {
		      	max-width: 103px;
		    }
		}
	}
	.switchers_holder {
		margin-top: 22px;
	    margin-bottom: 0;
	    float: right;
	    @media (max-width: @screen-xs-max) {
	      	margin-top: 7px;
	    }
	}
}


/* COUNTRIES FLAGS styles */
.flag_holder {
	width: 16px;
	height: 11px;
	display: inline-block;
	background-image: url('../images/flags/flags.png');
	background-repeat: no-repeat;
	&.lang_el {
		background-position: 0px 0px;
	}
	&.lang_gb {
		background-position: -16px 0px;
	}
	&.lang_zh {
		background-position: -32px 0px;
	}
	&.lang_de {
		background-position: -48px 0px;
	}
	&.lang_es {
		background-position: -64px 0px;
	}
	&.lang_fr {
		background-position: -80px 0px;
	}
	&.lang_hr {
		background-position: -96px 0px;
	}
	&.lang_hu {
		background-position: -112px 0px;
	}
	&.lang_it {
		background-position: -128px 0px;
	}
	&.lang_ja {
		background-position: -144px 0px;
	}
	&.lang_pl {
		background-position: -160px 0px;
	}
	&.lang_sr {
		background-position: -176px 0px;
	}
	&.lang_ru {
		background-position: -192px 0px;
	}
	&.lang_tr {
		background-position: -208px 0px;
	}
}


/* HEADER SEARCH FIELD styles */
.search_field_holder {
  	margin-top: 12px;
  	@media (max-width: @screen-sm-max) {
	    margin-top: 10px;
	    margin-bottom: 15px;
  	}
  	@media (max-width: @screen-xs-max) {
	    margin-top: 5px;
	    margin-bottom: 5px;
  	}
  	.form_search_field {
	  	background: none #ffffff;
	  	border-radius: 6px;
	  	padding: 6px 0;
	  	width: 100%;
	}
  	.typehead_holder {
  		position: relative;
  		width: 100%;
  		.clearfix();
  	}
  	.input_search {
  		width: 100%;
	    height: 30px;
	    border: none;
	    box-shadow: none;
	    font-size: 16px;
	    line-height: 24px;
	    padding: 3px 91px 3px 11px;
  	}
  	.btn_search {
		margin: 0 !important;
		border-radius: @customBtnBorderRadius !important;
		position: absolute;
		top: 0;
		right: 6px;
		z-index: 10;
    	i[class*='fontello-'] {
      		font-size: 20px;
      		@media (max-width: @screen-xs-max) {
			    font-size: 18px;
		  	}
    	}
  	}
}

/* TYPEHEAD SEARCH styles */
.twitter-typeahead {
	width: 100%;
	//float: left;
	display: block !important;
	pre {
		margin: 0;
	}
	.tt-dropdown-menu {
		background: none #ffffff;
		border-radius: 0 0 4px 4px;
		width: 100%;
		text-align: left;
		overflow: hidden;
		border: none;
		top: 31px !important;
		left: 0 !important;
		padding: 6px;
	}
	.tt-suggestion {
		width: 100%;
		border-radius: 6px;
		&:hover {
			background: none #4f95c7;
			a {
				color: #ffffff;
			}
		}
		a {
			display: block;
			padding: 6px; 
			color: @bodyFontColor;
		}
	}
}


/* TOP MENU styles */
.top_menu_holder {
	position: relative;
	margin-top: 5px;
	background: none @topMenuHolderBg;
	&:before {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		content: '';
		height: 5px;
		background: none @topMenuHolderBorderColor;
	}
}

.top_menu_navbar {
	border: none;
	margin-bottom: 0;
	min-height: 1px;
	padding: 0;
}

.top_menu_toggle {
    height: 28px;
    line-height: 28px;
    font-size: 24px;
    text-align: center;
    margin: 10px 0 4px 0;
    float: left;
    padding: 0;
    color: @topMenuTogleColor;
    &:before {
    	vertical-align: top;
    }
}

.top_menu {
	padding: 0;
	@media (max-width: @screen-sm-max) {
		width: 100%;
		margin-top: 5px;
	}
} 

.top_menu_list {
	margin: 0;
	> li {
		> a {
			display: block;
			font-size: 14px;
			line-height: 17px;
			padding: 13px 13px 15px 13px;
			border-top: 5px solid transparent;
			color: @topMenuListItemColor;
			font-weight: 700;
			text-transform: uppercase;
			@media (max-width: @screen-sm-max) {
				font-size: 13px;
				padding: 12px 9px;
				line-height: 15px;
			}
			&:hover {
				border-top: 5px solid @topMenuListItemBorderColor;
				background: none @topMenuListItemActiveBg;
			}
		}
		&.active {
			> a {
				border-top: 5px solid @topMenuListItemBorderColor;
				background: none @topMenuListItemActiveBg;
			}
		}
	}
}


/* HEADER LOGIN BOX styles */
.login_box {
	float: right;
	background: none @loginBoxBg;
	.clearfix();
	padding: 10px 15px 13px 15px;
	border-top: 5px solid @topMenuListItemBorderColor;
	line-height: 22px;
	font-size: 14px;
	@media (max-width: @screen-xs-max) {
		padding: 6px 12px 9px 12px;
		//background: none;
	}
	a {
		color: @topMenuListItemColor;
		font-weight: 600;
		text-transform: uppercase;
	}
}


/* DAYS SWITCHER SECTION */
.day_or_time_item {
	padding: 15px;
	background: none @dayOrTimeItemBg;
	position: relative;
	text-align: center;
	.transition(150ms);
	@media (max-width: @screen-xs-max) {
		padding: 12px;
	}
	&:first-child {
		border-radius: 6px 0 0 6px;
	}
	&:last-child {
		border-radius: 0 6px 6px 0;
	}
}

.day {
	text-align: center;
	font-size: 15px;
	color: @timesOfDayTitleColor;
	text-transform: uppercase;
	margin-bottom: 15px;
	@media (max-width: @screen-sm-max) {
		font-size: 13px;
	}
	@media (max-width: @screen-xs-max) {
		font-size: 11px;
	}
}

.temperature {
	font-size: 32px;
	font-weight: 700;
	line-height: 32px;
	text-align: center;
	color: @temperatureLineValueColor;
	&:last-child {
		margin-bottom: 0;
	}
	@media (max-width: @screen-xs-max) {
		font-size: 30px;
		line-height: 30px;
	}
	.degree {
		font-size: 17px;
		font-weight: 600;
		color: @temperatureLineDegreeColor;
		@media (max-width: @screen-xs-max) {
			font-size: 14px;
		}
	}
}

.humidity {
	font-size: 13px;
	color: @timeOfDayHumidityColor;
}
.precipitations {
	font-size: 13px;
	color: @timeOfDayPrecipitationsColor;
	i {
		margin-right: 3px;
	}
}

.section_days_switcher {
	padding: 30px 0;
	background-size: cover;
	&.weather_rainy {
		background-image: url('../images/bg/bg_weather_rainy.jpg');
	}
	&.weather_cloudy {
		background-image: url('../images/bg/bg_weather_cloudy.jpg');
	}
	.banner_block {
		.clearfix();
		@media (max-width: @screen-xs-max) {
			margin-bottom: 20px;
		}
	}
}

.times_of_day_holder {
	float: right;
	max-width: 420px;
	width: 100%;
	margin: 0 auto 15px auto;
	.clearfix();
	@media (max-width: @screen-xs-max) {
		float: none;
	}
	.day_or_time_item {
		background: none @timesOfDaySecondItemBg;
		&:first-child {
			background: none @timesOfDayFirstItemBg;
		}
		&:last-child {
			background: none @timesOfDayLastItemBg;
		}
	}
	.wind {
		font-size: 13px;
		line-height: @timeOfDayCompassSize;
		display: inline-block;
		@media (max-width: @screen-xs-max) {
			font-size: 10px;
		}
	}
	.compass_directions {
		width: @timeOfDayCompassSize;
		height: @timeOfDayCompassSize;
		float: left;
		margin-right: 5px;
		&.direction_n {
			background-position: center 0;
		}
		&.direction_nne {
			background-position: center -@timeOfDayCompassSize;
		}
		&.direction_ne {
			background-position: center -(2*@timeOfDayCompassSize);
		}
		&.direction_ene {
			background-position: center -(3*@timeOfDayCompassSize);
		}
		&.direction_e {
			background-position: center -(4*@timeOfDayCompassSize);
		}
		&.direction_ese {
			background-position: center -(5*@timeOfDayCompassSize);
		}
		&.direction_se {
			background-position: center -(6*@timeOfDayCompassSize);
		}
		&.direction_sse {
			background-position: center -(7*@timeOfDayCompassSize);
		}
		&.direction_s {
			background-position: center -(8*@timeOfDayCompassSize);
		}
		&.direction_ssw {
			background-position: center -(9*@timeOfDayCompassSize);
		}
		&.direction_sw {
			background-position: center -(10*@timeOfDayCompassSize);
		}
		&.direction_wsw {
			background-position: center -(11*@timeOfDayCompassSize);
		}
		&.direction_w {
			background-position: center -(12*@timeOfDayCompassSize);
		}
		&.direction_wnw {
			background-position: center -(13*@timeOfDayCompassSize);
		}
		&.direction_nw {
			background-position: center -(14*@timeOfDayCompassSize);
		}
		&.direction_nnw {
			background-position: center -(15*@timeOfDayCompassSize);
		}
	}
	p:last-child {
		margin-bottom: 0;
	}
}

.days_switcher_holder {
	float: right;
	max-width: 420px;
	display: block;
	margin: 0 auto;
	width: 100%;
	.clearfix();
	@media (max-width: @screen-xs-max) {
		float: none;
	}
	.item {
		padding: 10px 0;
		border: none;
		box-shadow: none;
		font-size: 14px;
		line-height: 20px;
		height: 40px;
		text-align: center;
		margin-left: 0 !important;
		font-weight: 400;
		text-transform: uppercase;
		background: none @daysSwitcherItemBg;
		position: relative;
		&.active {
			background: none @daysSwitcherItemActiveBg;
			color: @daysSwitcherItemActiveColor;
			&:hover {
				background: none @daysSwitcherItemActiveBg;
			}
		}
		&:hover {
			background: none @daysSwitcherItemHoverBg;
		}
		&:first-child:before, &:last-child:before {
			font-family: 'fontello';
			position: absolute;
			line-height: 16px;
			top: 12px;
		}
		&:first-child {
			padding-right: 5px;
			padding-left: 15px;
			border-radius: 6px 0 0 6px;
			&:before {
				left: 10px;
				content: '\e803';
			}
		}
		&:last-child {
			padding-left: 5px;
			padding-right: 15px;
			border-radius: 0 6px 6px 0;
			&:before {
				right: 10px;
				content: '\e804';
			}
		}
		@media (max-width: @screen-xs-max) {
			font-size: 10px;
		}
	}
}


/* SECTION MAIN CONTENT */
.section {
	padding: 30px 0 30px 0;
	position: relative;
	// &.top_padding_30 {
	// 	padding-top: 30px;
	// }
	> .container {
		position: relative;
		z-index: 10;
	}
}


/* BREADCRUNBS */
.breadcrumb {
  	background: none;
  	padding: 0;
  	margin-bottom: 25px;
  	font-size: 16px;
  	@media (max-width: @screen-xs-max) {
		font-size: 14px;
	}
	& > li + li:before {
		font-family: 'fontello';
		content: '\e812';
	    padding: 0 5px;
	}
}


/* DAY FORECAST HOLDER */
.day_forecast_holder {
	padding: 0;
	.clearfix();
	.day_or_time_item {
		cursor: pointer;
		@media (min-width: @screen-sm-min) {
			border-right: 1px solid @dayForecastItemBorderColor;
			.block_temperature {
				margin-bottom: 5px;
			}
		}
		@media (max-width: @screen-xs-max) {
			.clearfix();
			border-bottom: 1px solid @holdersBorderColor;
			border-radius: 0;
			&:last-child {
				border-bottom: none;
			}
			.subitem {
				// padding-left: 0;
				// padding-right: 0;
				// &:first-child {
				// 	padding-left: 0;
				// }
				&:last-child {
					//padding-right: 0;
					text-align: right;
				}
			}
			.day {
				margin: 15px 0 0 0;
				text-align: left;
				font-size: 9px;
				line-height: 13px;
			}
			.temperature {
				text-align: right;
				margin: 11px 0 0 0;
				font-size: 16px;
				line-height: 20px;
				height: 20px;
				.degree {
					font-size: 12px;
					line-height: 20px;
				}
			}
			.precipitations {
				font-size: 10px;
    			margin: 12px 0 0 0;
			}
		}
		&:first-child {
			border-right: none;
			@media (max-width: @screen-xs-max) {
				border-radius: 6px 6px 0 0;
			}
		}
		&:last-child {
			@media (max-width: @screen-xs-max) {
				border-radius: 0 0 6px 6px;
			}
		}
		&.active {
			background: none @dayForecastItemActiveBg;
			@media (min-width: @screen-sm-min) {
				&:before {
					width: 0;
					height: 0;
					content: '';
					border-style: solid;
					border-width: 0 12px 16px 12px;
					border-color: transparent transparent #fedab3 transparent;
					position: absolute;
					top: -16px;
					left: 50%;
					margin-left: -12px;
				}
			}
			&:hover {
				background: none @dayForecastItemActiveBg;
			}
			.day {
				color: @dayForecastItemActiveColor;
			}
			.temperature {
				color: @dayForecastItemActiveColor;
				.degree {
					color: @dayForecastItemActiveColor;
				}
			}
			.precipitations {
				color: @dayForecastItemActiveColor;
			}
		}
		&:hover {
			background: none @dayForecastItemHoverBg;
		}
		.subitem {
			//@media (min-width: @screen-sm-min) {
				padding-left: 0;
				padding-right: 0;
			//}
		}
		.day {
			color: @dayForecastItemDayTitleColor;
		}
		.weather_logo_holder {
			@media (max-width: @screen-xs-max) {
				max-width: 65px;
				margin-bottom: 0;
			}
		}
		.precipitations {
			margin-bottom: 0;
		}
	}
}


/* 24H FORECAST */
.forecast_24h_holder {
	.item {
		padding: 0 15px;
		text-align: center;
	}
	.time_interval {
		font-size: 14px;
		font-weight: 600;
		color: @timeIntervalColor;
		margin-bottom: 5px;
		display: inline-block;
	}
	.temperature {
		margin-bottom: 0;
	}
	.weather_status {
		font-size: 12px;
		font-weight: 600;
		color: @timeIntervalColor;
	}
}


/* 7 DAY FORECAST */
.forecast_7day_holder {
	padding-left: 8px;
	padding-right: 8px;
	.title {
		padding-left: 7px;
		float: left;
	}
	.switchers_holder {
		float: right;
	}
	.switcher {
		#gradient > .vertical(@start-color: @switchersGreyItemBgGradientFrom; @end-color: @switchersGreyItemBgGradientTo;);
		.btn {
			color: @switcherGreyColor;
			padding:4px;
			font-weight: 600;
		}
	}
	.item {
		text-align: center;
		&.col-custom-7 {
			padding-left: 7px;
			padding-right: 7px;
			@media (max-width: @screen-xs-max) {
				width: 33.3333%;
				&:last-child {
					width: 50%;
					float: none;
					.center-block();
				}
			}
		}
	}
	.day_and_date_block {
		padding: 8px;
