	}
	li {
		@media (max-width: @screen-xs-max) {
			float: none;
			width: 100%;
		}
		margin: 0;
		> a {
			position: relative;
			margin: 0;
			display: block;
			font-size: 14px;
			line-height: 20px;
			padding: 12px 15px 14px 15px;
			border-radius: 0;
			border-left: none;
			border-right: none;
			border-bottom: none;
			border-top: 5px solid transparent;
			color: @prefooterTabsNavigationItemColor;
			&:hover {
