					display: none;
				}
			}
		}
		&.active {
			> a, > a:hover, > a:focus {
				background: none @prefooterTabsNavigationItemActiveBg;
				border-left: none;
				border-right: none;
				border-bottom: none;
				border-top: 5px solid @prefooterTabsNavigationItemActiveBorderColor;
				color: @prefooterTabsNavigationItemColor;
