/* MARQUEE TEXT */
.section_marquee_text {
	background: none @marqueTextBg;
}
.marquee {
  	width: 100%;
  	overflow: hidden;
  	padding: 16px 0;
  	position: relative;
  	.user-select(none);
  	cursor: default;
  	@media (max-width: @screen-xs-max) {
    	padding: 8px 0;
  	}
  	&:before, &:after {
  		position: absolute;
  		top: 0;
  		width: 100px;
  		height: 100%;
  		content: '';
  		z-index: 10;
  	}
  	&:before {
  		left: 0;
  		#gradient > .horizontal(@start-color: @marqueTextOverlayColorSolid; @end-color: @marqueTextOverlayColorTransparent; @start-percent: 0%; @end-percent: 100%);
  	}
  	&:after {
  		right: 0;
  		#gradient > .horizontal(@start-color: @marqueTextOverlayColorTransparent; @end-color: @marqueTextOverlayColorSolid; @start-percent: 0%; @end-percent: 100%);
  	}
  	.js-marquee {
  		font-size: 16px;
	  	line-height: 20px;
	  	color: @marqueTextColor;
		@media (max-width: @screen-xs-max) {
	    	line-height: 16px;
	    	font-size: 14px;
	  	}
  	}
}


