.weather_logo_holder {
	margin-left: auto;
	margin-right: auto;
	margin-bottom: 15px;
}

.weather_logo_ratio_box {
	position: relative;
	padding-bottom: 67.88%;
	height: 0;
}

.weather_logo {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-size: 100%;
	background-repeat: no-repeat;
}


