/* Fontello */
@font-face {
  font-family: 'fontello';
  src: url('../fonts/fontello.eot?90219663');
  src: url('../fonts/fontello.eot?90219663#iefix') format('embedded-opentype'),
       url('../fonts/fontello.woff?90219663') format('woff'),
       url('../fonts/fontello.ttf?90219663') format('truetype'),
       url('../fonts/fontello.svg?90219663#fontello') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="fontello-"]:before, [class*="fontello-"]:before {
  font-family: "fontello";
  font-style: normal;
  font-weight: normal;
  speak: none;
  display: inline-block;
  text-decoration: inherit;
  width: 1em;
  text-align: center;
  font-variant: normal;
  text-transform: none;
  line-height: 1em;
  //vertical-align: middle;
  vertical-align: inherit;
}

